      //     api.get_user_info({
      //         headers:{
      //           'Accept':'application/json',
      //           'Authorization':"Bearer "+access_token,
      //         }
      //     }).then(res=>{
      //       console.log(res.data);
      //     })
      // });
    },
    watch: {
      $route: 'fetch_data'
    },
    components: {
      XHeader,
      Tabbar,
      TabbarItem,
      Search,
      Popup,
      Group,
      XInput,
      XButton
    }
